/* Reset CSS */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, sans-serif;
  }
  
  /* Keep the navigation bar and header untouched */
  
  /* Main User Container */
  .user-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    background: var(--main-color);
    color: white;
  }
  
  /* Content Area */
  .dashboard-content {
    text-align: center;
    margin-top: 12px;
  }
  
  /* Welcome Text */
  .welcome-text {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 25px;
  }
  
  /* Button Container */
  .btn-container {
    display: flex;
    gap: 20px;
    justify-content: center;
    margin-top: 20px;
  }
  
  /* Primary & Secondary Buttons */
  .btn-primary,
  .btn-secondary {
    padding: 12px 30px;
    border-radius: 8px;
    font-size: 1rem;
    text-align: center;
    text-decoration: none;
    font-weight: bold;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  /* Primary Button - Start Call */
  .btn-primary {
    background-color: white;
    color: var(--main-color);
    border: 2px solid white;
  }
  
  .btn-primary:hover {
    background-color: var(--main-color);
    color: white;
  }
  
  /* Secondary Button - Join Call */
  .btn-secondary {
    background-color: var(--main-color);
    color: white;
    border: 2px solid white;
  }
  
  .btn-secondary:hover {
    background-color: white;
    color: var(--main-color);
  }
   /* Texte sous le message de bienvenue */
.info-text {
    font-size: 1.8rem;
    font-weight: 400;
    color: #b7c0c0; /* Gris légèrement plus sombre */
    text-align: center;
    margin-top: 8px; /* Espacement avec le titre */
    margin-bottom: 25px; /* Espacement avant les boutons */
    max-width: 600px;
  }
  
  /* Style spécial pour MyMeet */
  .brand {
    font-weight: bold;
    color: rgba(239, 243, 0, 0.788);
  }
  
/* Logo avec une bordure circulaire blanche */
.logo-meet {
    
    margin-top: 60px;
    width: 60px; /* Ajustable selon la taille du logo */
    height: 60px;
    border-radius: 50%; /* Rend la bordure circulaire */
    border: 3px solid rgba(239, 243, 0, 0.788); /* Bordure blanche */
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px; /* Espacement interne */
    transition: transform 0.3s ease-in-out;
  }
  
  /* Effet au survol */
  .logo-meet:hover {
    transform: scale(1.1); /* Légère augmentation de taille au survol */
  }
  
  /* Logo image à l'intérieur */
  .logo-meet img {
    color: aqua;
    width: 100%; /* Ajuste l’image à l’intérieur */
    height: auto;
    object-fit: contain;
  }
  
 
  
  